/*
    RESET CSS
   http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    border: 0;
    font: inherit;
    vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}

body {
    line-height: 1;
}

ol, ul {
    list-style: none;
}

blockquote, q {
    quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

/*DEFAULT REACT CSS*/
/* https://stackoverflow.com/a/32805276 */
html {
    /*UI will not scroll on X axis*/
    overflow-x: hidden;
    font-size: calc(0.75em + 1vmin);
    width: 100%;
    height: 100%;
}

* {
    padding: 0;
    margin: 0;
    text-decoration: none;
    list-style: none;
    box-sizing: border-box;
    font-family: 'Lato', sans-serif;
}

body {
    width: 100%;
    height: 100%;
    /*background: #E6E6FA; LAVANDER */
    font-size: 16px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
/**/
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input, button {
    font-size: inherit;
}

#root {
    width: 100%;
    height: 100%;
}

.fade-enter {
    opacity: 0;
    /*transform: translateY(1000px);*/
}
.fade-enter-done {
    opacity: 1;
    /*transform: translateY(0);*/
    transition: all 300ms cubic-bezier(0.4, 0.0, 1, 1);
}
.fade-exit {
    opacity: 1;
    /*transform: translateY(0);*/
}
.fade-exit-done, .fade-exit-active {
    opacity: 0;
    /*transform: translateY(1000px);*/
    transition: all 250ms cubic-bezier(0.0, 0.0, 0.2, 1);
}
